.site-header {
  background-color: #1a1a1a;
  color: white;
  padding: 20px;
  text-align: center;
}

.nav-button {
  background: none;
  color: white;
  border: none;
  font-size: 16px;
  margin: 0 15px;
  cursor: pointer;
  font-weight: bold;
}

.nav-button:hover {
  text-decoration: underline;
}

.search-section {
  margin: 30px 0;
  text-align: center;
}

.search-section input {
  padding: 10px;
  width: 300px;
  font-size: 16px;
  margin-right: 10px;
}

.search-button {
  padding: 10px 20px;
  background-color: #0071e3;
  color: white;
  border: none;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
}

.listing-section {
  padding: 20px;
  text-align: center;
}

.property-grid {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.property-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  width: 220px;
  text-align: left;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.property-card img {
  width: 100%;
  border-radius: 5px;
}
